
import Group from 'vux/src/components/group/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { fetchEnvelopeRecord } from '../../api'
import infiniteScroll from 'vue-infinite-scroll'
export default {
name: 'EnvelopeRecord',
components: {
Group,
XTable,
XButton
},
directives: {
infiniteScroll
},
data () {
return {
tableStyle: {
color: '#4a4a4a',
'font-size': '12px',
'background-color': '#fff'
},
offset: 0,
limit: 20,
records: [],
totalCount: 0,
busy: true
}
},
created () {
fetchEnvelopeRecord({offset: this.offset, limit: this.limit}).then(res => {
this.records = res.results
this.totalCount = res.count
this.busy = false
}).catch(() => {})
},
methods: {
loadMore () {
if (this.busy || this.offset > this.totalCount) {
return
}
this.offset = this.offset + this.limit
fetchEnvelopeRecord({offset: this.offset, limit: this.limit}).then(res => {
this.records = this.records.concat(res.results)
}).catch(() => {})
}
}
}



